<template>
    <div class="coins">
        <div class="header-block">
            <p class="cabinet-header">{{$localizeFilter('coins')}}</p>
            <p class="cabinet-text">{{$localizeFilter('coins_text')}}</p>
        </div>

        <div class="chart-container" v-for="(coin, idx) in coins" v-bind:key="idx">
            <div class="chart-header">
                <img :src="require('@/assets/newImages/coins/icons/' + coin.coinTicker.toLowerCase() + '_icon.png')" class="coin-icon">
                <p @click="openCoinPage(coin.coinName)">{{ coin.coinName }}</p>

                <!-- <a :href="coin.buyCoinLink || 'https://t.me/obmenMaks'" target="_blank" v-if="coin.coinTicker!='DOM'"> -->
                    <button class="buy-button" @click="buyCoin(coin.coinTicker)" v-if="coin.coinTicker!='DOM'">
                        <span style="text-shadow: 4px 7px 15px rgba(0, 0, 0, 0.29);">{{$localizeFilter('buy')}}</span>
                    </button>
                <!-- </a> -->

            </div>
            <Chart :coinName="coin.coinName" :coinTicker="coin.coinTicker" :animateChart="false"/>
        </div>

    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import Chart from "@/components/Chart.vue"

export default defineComponent({
    components: { Chart },
    data: ()=>({
        width: 0,
        coins: [
            {
                coinName: 'OilGasCoalToken',
                coinTicker: 'OGCT'
            },
            {
                coinName: 'InternetCoin',
                coinTicker: 'ITC'
            },
            {
                coinName: 'GadgetCoin',
                coinTicker: 'GDC'
            },
            {
                coinName: 'CapitalWhalePresaleToken',
                coinTicker: 'CWPT'
            },
            {
                coinName: 'DOM',
                coinTicker: 'DOM'
            }
        ],
        showHeatMap: false,
        heatMapContainerStyle: null
    }),
    setup() {

    },

    methods: {
        openCoinPage(coinName){
            window.open(location.origin+'/#/coins/'+coinName, '_blank');
        },
        async buyCoin(coinTicker) {
            await this.$store.dispatch('setPaymentCoinTicker', {coinTicker: coinTicker})
            this.$nextTick(()=>{
                this.$store.dispatch('setShowPaymentCoinModule', {value: true})
            })
        }
    },

    // async loadData(){
        
    // },
    mounted(){
        
    }
})
</script>

